.rainbowCircle-component  {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-image: linear-gradient(45deg, #00C0FF 0%, #FFCF00 49%, #FC4F4F 100%);
    padding: 3px;
    border-radius: 5px;
    width: 90vw;
    margin-left: 5vw;
    border: none;
}

.slider {
    background: rgba(46, 46, 46, 0.9);
    color: rgb(226, 226, 226);
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.title {
    background: rgba(46, 46, 46, 0.9);
    color:rgb(112, 112, 112);
    margin-top: 10px;
    width: max-content;
    margin-left: 5vw;
    padding: 1px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    border-radius: 5px;
}